.mess__username {
  color: rgba(0, 0, 0, 0.4);
  font-size: 14px;
  font-weight: normal;
  line-height: 1.28;
  margin-bottom: 1px;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: ltr;
  text-align: left;
  margin-left: auto;
}

.messageBox {
  position: relative;
  z-index: -1;
}
.message {
  margin-top: 1px;
  margin-right: 0px;
  margin-bottom: 1px;
  margin-left: 0px;
  padding-top: 6px;
  padding-right: 12px;
  padding-bottom: 6px;
  padding-left: 12px;
  width: fit-content;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.34;
  font-size: 15px;
  text-align: left;
}

.message__user {
  margin-left: auto;
}

.message__userCard {
  background-color: #0084ff;
  color: #fff;
}

.message__guestCard {
  background-color: #f1f0f0;
  color: black;
}
